import { spawn } from "child_process";
import path from "path";
import fs from "fs";
import os from "os";

export default async function handler(req, res) {
    if (req.method === "POST") {
        try {
            const { videoUrl } = req.body;

            if (!videoUrl) {
                return res.status(400).json({ error: "No video URL provided." });
            }

            // Define file paths
            const tempDir = os.tmpdir();
            const videoFileName = `temp-video-${Date.now()}.mp4`;
            const videoFilePath = path.join(tempDir, videoFileName);

            console.log("Downloading video...");

            // 1. Download the video using yt-dlp
            await new Promise((resolve, reject) => {
                const downloadProcess = spawn("yt-dlp", ["-o", videoFilePath, videoUrl]);

                downloadProcess.on("close", (code) => {
                    if (code !== 0) {
                        console.error("yt-dlp failed to download video.");
                        return reject(new Error("Failed to download video"));
                    }
                    console.log("Video downloaded successfully:", videoFilePath);
                    resolve();
                });
            });

            // 2. Transcribe the video using Whisper
            console.log("Starting Whisper transcription...");
            const whisperOutputFile = `${videoFilePath}.json`;

            await new Promise((resolve, reject) => {
                const whisperProcess = spawn("whisper", [videoFilePath, "--output_format", "json", "--output_dir", tempDir]);

                whisperProcess.on("close", (code) => {
                    if (code !== 0) {
                        console.error("Whisper failed to transcribe video.");
                        return reject(new Error("Failed to transcribe video"));
                    }
                    console.log("Whisper transcription completed:", whisperOutputFile);
                    resolve();
                });
            });

            // 3. Read the generated JSON file
            const transcriptData = fs.readFileSync(whisperOutputFile, "utf-8");
            const transcriptJson = JSON.parse(transcriptData);
            const transcript = transcriptJson.text || "No transcript available.";

            // 4. Clean up temp files
            fs.unlinkSync(videoFilePath);
            fs.unlinkSync(whisperOutputFile);

            console.log("Transcript successfully extracted.");
            return res.status(200).json({ transcript });
        } catch (error) {
            console.error("Error during transcription:", error);
            return res.status(500).json({ error: "Internal server error." });
        }
    } else {
        return res.status(405).json({ error: "Method not allowed." });
    }
}
